#products {
    width: 100%;
    height: 110vh;
    box-sizing: border-box;
    font-family: 'Lato';
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding-top: 10px; */
    background-color: #ffffff;
    h1 {
        font-family: 'Alfa Slab One';
        letter-spacing: 2px;
        color: #3d2514;
        font-size: 3rem;
    }
    p {
        font-family: 'Bebas Neue';
        letter-spacing: 1px;
        color: #3d2514;
        font-size: 0.8rem;
        margin-top: -15px;
        margin-bottom: 35px;
    }
}

.product-container {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
}

@media(max-width:1190px) {
    #products {
        height: auto;
    }
    .product-container {
        flex-wrap: wrap;
    }
}

@media(max-width:970px) {
    #products {
        margin-bottom: 100px;
    }
}