#about {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0px 5% 0px 5%;
  position: relative;
}

.about-text {
  width: 50%;
  color: #fff;
  text-align: left;
}
.about-text h1 {
  font-size: 3.5rem;
  color: #3d2514;
  font-weight: 500;
  margin: 0px;
  padding: 0px;
  font-family: "Alfa Slab One";
}
.about-text p {
  font-family: "Bebas Neue";
  letter-spacing: 1px;
  color: #252525;
  font-size: 1.2rem;
}
.about-text button {
  margin-top: 20px;
  width: 140px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-family: "Lato";
  background-color: #bf2222;
  box-shadow: 5px 10px 30px rgba(255, 103, 2, 0.336);
  border-radius: 5px;
  color: #ffffff;
}
.about-text button:hover {
  background-color: transparent;
  transition: all ease 0.5s;
  color: #3d2514;
  border: 2px solid #3d2514;
}
.about-img {
  padding-left: 5%;
  width: 40%;
}
.about-img img {
  width: 100%;
}

@media (max-width: 1190px) {
  .about-image img {
    height: 400px;
  }
}
@media (max-width: 970px) {
  #about {
    justify-content: center;
    justify-items: center;
    padding-top: 0px;
    text-align: center;
    margin-top: 0px;
  }

  .about-text {
    width: 90%;
    text-align: center;
  }
  .about-text h1 {
    font-size: 3.5rem;
  }
  .about-text p {
    width: 100%;
  }
  .about-text button {
    margin-left: 30%;
  }
  .about-img {
    display: none;
  }
  .about img {
    width: 100%;
    padding: 0;
  }
}
@media (max-width: 410px) {
  .about-text h1 {
    font-size: 2.5rem;
  }
}

